import ErmjBaseHandler from "./ErmjBaseHandler";

export default class ErmjAutoHandler extends ErmjBaseHandler{
    protected execute(msg){
        let localSeat = this.SitHelper.serverSToLocalN(msg._src);
        let isAuto = msg._para.auto_play == 1;
        this.mainUI.callPlayer(localSeat, 'showAutoSign', isAuto);
        if(localSeat == 0){
            this.mainUI.askBtnView.setAutoPlayBtnShow(!isAuto);
            this.mainUI.doResetMj();
            this.context.isCmdAuto = isAuto
        }
    }

    protected executeSync(msg){
        this.execute(msg);
    }
}